.container {
  margin: 2rem;
}

.persons {
  display: flex;
  flex-direction: column;
}

.person {
  display: flex;
}

.person--name {
  display: inline-flex;
  width: 140px;
}

.person--number {
  display: inline-flex;
  width: 125px;
}

.label {
  width: 140px;
  display: inline-flex;
}

h1,
h2 {
  font-size: 1.5rem;
  margin: 1rem 0 0.5rem 0;
}

.notification {
  color: #19a919;
  background: #c6f9c6;
  border: 1px solid #19a919;
  border-radius: 5px;
  padding: 7px;
  margin-bottom: 10px;
}

.error {
  color: #f50000;
  background: #ffdddd;
  border: 1px solid #f50000;
  border-radius: 5px;
  padding: 7px;
  margin-bottom: 10px;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 20px;
}
h1 {
  font-size: 40px;
  font-weight: bold;
  margin-left: 20px;
}
button {
  border-style: none;
  border: rgb(80, 80, 80) solid 1px;
  border-radius: 10px;
  background-color: rgb(199, 199, 199);
  color: black;
  cursor: pointer;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
  box-shadow: 0px 0px 10px rgb(77, 77, 77);
  margin-left: 20px;
}
button:hover {
  background-color: rgb(77, 77, 77);
  color: white;
}
input {
  border-style: none;
  border: rgb(80, 80, 80) solid 1px;
  border-radius: 10px;
  background-color: rgb(214, 214, 214);
  color: black;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
  box-shadow: 0px 0px 10px rgb(77, 77, 77);
  margin-left: 20px;
  margin-bottom: 20px;
}
ul {
  list-style-type: none;
  overflow: hidden;
  background-color: rgb(199, 199, 199);
  box-shadow: 0px 0px 10px rgb(77, 77, 77);
}

li {
  font-size: 20px;
  color: black;
}
